  position: relative;
}

.nav__list--features li:before {
  content: "â€¢";
  color: #000;
  font-size: 15px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.nav__list--benefits {
  color: #333;
