}

.nav__item {
  width: fit-content;
  transition: all .3s;
}

.nav__item--features:hover {
  transform: translateX(-15px);
}

.nav__link {
  color: #333;
  border: 1px solid #0000;
  width: fit-content;
  text-decoration: none;
  transition: border .3s;
  display: block;
  position: relative;
}

.nav__link--aside:after {
  content: "";
  transform-origin: 0;
  background-color: #333;
  width: 100%;
  height: 1px;
  transition: all .3s;
  display: block;
  position: absolute;
  bottom: -5px;
  transform: scale(0);
}

.nav__link--aside:hover:after {
  transform: scale(1);
}

.nav__link--benefits {
  transition: all .3s;
}

.nav__link--benefits:hover {
  font-weight: 700;
  transform: scale(1.1);
}

.nav__link--footer {
  transition: all .3s;
}

.nav__link--footer:hover {
  border: 1px solid #333;
  transform: scale(1.25);
}

.nav__image {
  width: 24px;
  height: 24px;
  display: block;
}

